
    import {computed, defineComponent, reactive, watch} from "vue";
    import {useRouter} from "vue-router";

    export default defineComponent({
        name: "Create",
        setup() {
            const data = reactive({
                height: 3,
                width: 3,
                goal: 3,
                isLocal: true,
                max: 12
            });

            watch(
                () => data.height,
                height => {
                    if (height > data.max) {
                        data.height = data.max;
                    }
                }
            );

            watch(
                () => data.width,
                width => {
                    if (width > data.max) {
                        data.width = data.max;
                    }
                }
            );

            watch(
                () => data.goal,
                goal => {
                    const max = data.height >= data.width ? data.height : data.width;
                    if (goal > max) {
                        data.goal = max;
                    }
                }
            );

            const link = computed(() => {
                let url = "/game";
                if (data.isLocal) {
                    url = "/game/local";
                }
                return url;
            });

            const rows = computed(() => {
                let value = "";
                for (let i = 0; i < data.height; i++) {
                    value += " 1fr";
                }
                return value;
            });

            const cols = computed(() => {
                let value = "";
                for (let i = 0; i < data.width; i++) {
                    value += " 1fr";
                }
                return value;
            });
            const router = useRouter();

            function CreateGame() {
                localStorage.setItem("table_width", data.width.toString());
                localStorage.setItem("table_height", data.height.toString());
                localStorage.setItem("table_goal", data.goal.toString());
                router.push(link.value);
            }

            return {
                data,
                rows,
                cols,
                CreateGame
            };
        }
    });
